<template>
  <Header class="header" />
  <router-view id="router-view" />
</template>

<script>
import Header from "@/features/Header.vue";
export default {
  components: { Header },
  mounted() {
    document.title = "dvegasa";
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        document.title = to.meta.title || "dvegasa";
      },
    },
  },
};
</script>

